.github
{
  font-family: consolas;
  margin-left: 16%;
    position: relative;
    display: inline-block;
    padding: 15px 30px;
    color: #2196f3;
    text-transform: uppercase;
    letter-spacing: 4px;
    text-decoration: none;
    font-size: 15px;
    font-style: bold;
    overflow: hidden;
    transition: 0.2s;
    border-radius: 8px;
    
}
.github:hover
{
    color: #ffffff;
    background: #21b8f3;
    /* box-shadow: 0 0 5px #21b8f3, 0 0 20px #21b8f3, 0 0 40px #21b8f3; */
    transition-delay: 1s;
}
.github span
{
    
    position: absolute;
    display: block;
}
.github span:nth-child(1)
{
    top: 0;
    left: -100%;
    width: 50%;
    height: 2px;
    background: linear-gradient(270deg,transparent,#21b8f3);
}
a:hover span:nth-child(1)
{
    left: 100%;
    transition: 1s;
} 
.github span:nth-child(2)
{
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg,transparent,#21b8f3);
}
.github:hover span:nth-child(2)
{
    right: 100%;
    transition: 1s;
    transition-delay: 0.5s;
}
.github span:nth-child(3)
{
    top: -100℅;
    right: 0%;
    width: 2px;
    height: 100%;
    background: linear-gradient(270deg,transparent,#21b8f3);
}
.github:hover span:nth-child(3)
{
    top: 100%;
    transition: 1s;
    transition-delay: 0.25s;
}
.github span:nth-child(4)
{
    bottom: -100℅;
    left: 0%;
    width: 2px;
    height: 100%;
    background: linear-gradient(270deg,transparent,#21b8f3);
}
.github:hover span:nth-child(4)
{
    bottom: 100%;
    transition: 1s;
    transition-delay: 0.75s;
}
